
document.addEventListener("DOMContentLoaded", () => {
    initApp();
});

function initApp() {
    setConcertDate();
    createGallery();
    setFooter();
    scrollNav();
    navFixed();
}

function navFixed() {
    const bar = document.querySelector(".header");
    const festivalInfo = document.querySelector(".info-festival");
    const body = document.querySelector("body");

    window.addEventListener("scroll", () => {
        if (festivalInfo.getBoundingClientRect().bottom < 0) {
            bar.classList.add("fixed");
            body.classList.add("body-scroll");
        } else {
            bar.classList.remove("fixed");
            body.classList.remove("body-scroll");
        }
    });
}

function scrollNav() {
    const links = document.querySelectorAll(".nav a");
    links.forEach(link => {
        link.addEventListener("click", e => {
            e.preventDefault();

            const scrollSection = e.target.attributes.href.value;
            const section = document.querySelector(scrollSection);

            section.scrollIntoView({
                behavior: "smooth",
            });
        });
    });
}

function createGallery() {
    const gallery = document.querySelector(".gallery-images");

    for (let i = 1; i <= 12; i++) {
        const image = document.createElement("picture");

        image.innerHTML = `
            <source srcset="build/img/thumb/${i}.avif" type="image/avif">
            <source srcset="build/img/thumb/${i}.webp" type="image/webp">
            <img loading="lazy" src="build/img/thumb/${i}.jpg" width="200" height="300" alt="Gallery image">
        `;
        image.onclick = () => {
            showImage(i);
        }

        gallery.appendChild(image);
    }
}

function showImage(id) {
    const image = document.createElement("picture");
    image.innerHTML = `
        <source srcset="build/img/larger/${id}.avif" type="image/avif">
        <source srcset="build/img/larger/${id}.webp" type="image/webp">
        <img loading="lazy" src="build/img/larger/${id}.jpg" width="200" height="300" alt="Gallery image">
    `;

    //overlay to add image to HTML
    const overlay = document.createElement("div");
    overlay.appendChild(image);
    overlay.classList.add("overlay");
    overlay.onclick = () => {
        overlay.remove();
        const body = document.querySelector("body");
        body.classList.remove("fixed-body");
    }

    //btn to close modal
    const bntClose = document.createElement("p");
    bntClose.textContent = "X";
    bntClose.classList.add("btn-close");
    bntClose.onclick = () => {
        overlay.remove();
        const body = document.querySelector("body");
        body.classList.remove("fixed-body");
    }
    overlay.appendChild(bntClose);

    const body = document.querySelector("body");
    body.appendChild(overlay);
    body.classList.add("fixed-body");
}

function setConcertDate() {
    const dateFestival = document.querySelectorAll(".date-festival");

    dateFestival.forEach(element => {
        const date = new Date();
        date.setMonth(date.getMonth() + 1);

        if (element.classList.contains("day-1")) {
            date.setDate(date.getDate() + 2);
            element.textContent = date.toLocaleDateString("CO", {
                month: "long",
                day: "2-digit",
                year: "numeric"
            });
            return;
        }
        if (element.classList.contains("day-2")) {
            date.setDate(date.getDate() + 3);
            element.textContent = date.toLocaleDateString("CO", {
                month: "long",
                day: "2-digit",
                year: "numeric"
            });
            return;
        }

        element.textContent = date.toLocaleDateString("CO", {
            month: "long",
            year: "numeric"
        }) + " Bogota, Colombia";
    });
}

function setFooter() {
    const footer = document.querySelector(".footer");
    const p = document.createElement("p");
    p.textContent = `Rock & EDM Festival. All rights reserved ${new Date().getFullYear()}`
    footer.appendChild(p);
}



